<script>
import { onMounted, ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import addico from '../assets/sections/expand.svg'
import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      pageModel: {
        current: 1,
        size: 20,
      },
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },

      totalSupply: 10000,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 10,
      mintNumber: "",
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
        tip7:false,
        tip8:false,
        tip9:false,
        tip10:false,
        tip11:false,
        tip12:false,
        tip13:false,
        tip14:false,
        tip15:false,
        tip16:false,
        tip17:false,
        tip118:false,
        tip119:false,
      },
      imgAdd:addico,
      imgDel:delico,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;

    // 
    const omitshow = function (value, start, end) {
      if (value != "" && value != null && typeof value != "undefined") {
        var str =
          value.substring(0, start) +
          "..." +
          value.substring(value.length - end);
        return str;
      }
    };
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    //
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(
          state.connect.address
        );
        let amount = state.mintNumber * state.mintPrice;

        if (Number(balance) < amount) {
          alert("Insufficient wallet balance");
          console.log(balance, amount);
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    const benefitsList = [
      {title: 'Community-<br>first values', content: 'When you hold a DAW,  youre not just purchasing digital art. DAW membership means being part of a diverse network of supportive people that empower each other to succeed in Web3 and beyond.'},
      {title: 'Invitations to IRL and metaverse events', content: 'Together, were raising the bar for the quality of community, art, and web3 experiences. Get invitations to VIP world-class IRL and metaverse events, exhibitions and experiences.'},
      {title: 'Members-only offers from brands', content: 'Brands that collaborate with TronApeWives give our members insider access to promotions and benefits. New and exciting brand collaborations are underway. Holders can find current promotions in the members-only section.'},
      {title: 'Exclusive airdrops', content: 'DAW holders get exclusive airdrops from DAW events, collaborations with emerging artists, partnerships with brands, limited-edition releases from House of DAW, and future collections.'},
      {title: 'Members-only digital fashion brand', content: 'Express yourself in web3 with style for the metaverse and beyond with the House of DAW digital fashion brand. Holders access private wearables sales and IRL limited-edition capsule collections.'},
      {title: 'Own full IP rights to your DAW', content: 'When buying a DAW, individuals and entities are automatically granted the full IP and commercial usage rights. This means theres no limit to what you can do with your DAW commercially. We provide guidance for businesses utilizing their DAW IP.'},
    ];
    const timeImgList = [
      {img: 'The-Florist-1.png', title: 'THE FLORIST.', content: 'Fashion Connoisseur.'},
      {img: 'THE-GARDENER.png', title: 'THE GARDENER.', content: 'OG Traditional Art Market Collector.'},
      {img: 'archer.png', title: 'ARCHER MCCALL.', content: 'The Creative Outlaw.'},
      {img: 'ANNA._.png', title: 'ANNA.', content: 'Web Master.'},
    ];
    let active = ref(-1);
    const methods = {
      mouseOver(index) {
        active.value = index
      },
      mouseLeave() {
        active.value = -1
      }
    };
    onMounted(() => {
      setTimeout(async function () {
        getTotalSupply();
      }, 1000);
    });
    return {
      ...toRefs(state), //  state  state 
      ...methods,
      connectWallet,
      disconnect,
      getTotalSupply,
      omitshow,
      getMint,
      benefitsList,
      timeImgList,
      active
    };
  },
};
</script>



